<template>
  <div class="InTransitManage">
    <div class="leftMenu">
      <el-form style="width:100%" :model="form" label-width="60px">
        <el-form-item label="渠道">
          <el-select
            filterable
            style="width:100%"
            v-model="form.platform"
            placeholder="请选择渠道"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in channels"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="国家">
          <el-select
            filterable
            @change="changeOpt('country')"
            style="width:100%"
            v-model="form.country"
            placeholder="请选择渠道"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in countrys"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="站点">
          <el-select
            filterable
            @change="changeOpt('site')"
            style="width:100%"
            v-model="form.store"
            placeholder="请选择站点"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in stores"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="类型">
          <el-select filterable style="width:100%" v-model="form.type">
            <el-option
              v-for="(item, i) in types"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="仓库">
          <el-select
            style="width:100%"
            v-model="form.warehouse"
            placeholder="请选择仓库"
            filterable
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in warehouses"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="产品">
          <el-select
            style="width:100%"
            v-model="form.commodity_name"
            placeholder="请选择编码+产品"
            filterable
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in products"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search" type="primary" @click="searchInfoBtn"
            >搜索</el-button
          >
        </el-form-item>
      </el-form>
    </div>
    <div class="rightMenu" style="position:relative;">
      <h2>在途货柜盘点</h2>
      <div style="position:relative">
        <div
          :style="{ visibility: loading ? 'visible' : 'hidden' }"
          class="loadingMask"
        >
          <i class="el-icon-loading"></i><span>加载中...</span>
        </div>
        <div class="on_way_table">
          <table class="IntainsitionTable">
            <thead>
              <tr style="position:sticky">
                <th style="z-index:16" class="fixed-column">
                  序号
                </th>
                <th style="z-index:15" class="t_head_td fixed-column">
                  <div class="t_head">
                    <span
                      style="    position: absolute;
    left: 20px;
    bottom: 0px;"
                      >产品</span
                    >
                    <span
                      style="    background: rgb(229, 229, 229);
    width: 1px;
    height: 175px;
    position: absolute;
    transform: rotate(-72deg);
    top: -60px;
    left: 83px;"
                    ></span>
                    <span
                      style="position: absolute;
    top: 0px;
    right: 12px;"
                      >货柜仓库</span
                    >
                  </div>
                </th>
                <th style="z-index:14" class="fixed-column">
                  sku
                </th>
                <th style="z-index:13" class="fixed-column">
                  库存总计
                </th>
                <th style="z-index:12" class="fixed-column">
                  FBA合计
                </th>
                <th style="z-index:11" class="fixed-column">
                  FBM合计
                </th>
                <th
                  style="z-index:10"
                  class="fixed-column"
                  v-for="(item, i) in warehouses_list"
                  :key="i"
                >
                  {{ item }}
                </th>
              </tr>
            </thead>
            <tbody>
              <!--  -->
              <tr
                v-show="!item.hide"
                @click="clickRow(item, idx)"
                v-for="(item, idx) in tableData"
                :key="idx"
              >
                <td style="z-index:6" class="fixed-column">
                  {{ item.index }}
                </td>
                <td style="z-index:5" class="fixed-column">
                  {{ item.product_code }}
                </td>
                <td style="z-index:4" class="fixed-column">
                  {{ item.sku }}
                </td>
                <td style="z-index:3" class="fixed-column">
                  {{ item.stock_num }}
                </td>
                <td style="z-index:2" class="fixed-column">
                  {{ item.fba }}
                </td>
                <td class="fixed-column" style="z-index:1">
                  {{ item.fbm }}
                </td>
                <td v-for="(i, j) in warehouses_list" :key="j">
                  {{ item[i] }}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <el-pagination
        v-if="totalNum > 0"
        @current-change="changePage"
        :current-page.sync="currentPage"
        :page-size="50"
        layout="total, prev, pager, next,jumper"
        :total="totalNum"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchNew: true,
      loading: false,
      currentPage: 1,
      totalNum: 0,
      // 默认显示总计后六列
      // showCol: true,
      searchlist: [],
      data1: [],
      productlist: [],
      realProductList: [],
      channels: [],
      stores: [],
      countrys: [],
      types: ["钢木", "魔片"],
      warehouses: ["海外仓", "FBA"],
      products: [],
      // table部分
      warehouses_list: [], // 表头仓库
      hj: [],
      tableData: [],
      zonji: {},
      //选中的值
      form: {
        platform: "all",
        store: "all",
        country: "all",
        type: "钢木",
        warehouse: "all",
        product: "all"
      }
    };
  },
  methods: {
    rowClick(row, column, event) {
      if (!row.index) {
        return;
      }
      // console.log("点击行");
      // console.log(row, column, event);
      let ItemIdx = this.tableData.findIndex(i => {
        return i.index === row.index;
      });
      let Item = this.tableData[ItemIdx];
      let len = Item.sku_data.length;
      // console.log(Item, len);
      // ItemIdx :1  len :3 2-4
      for (let i = ItemIdx + 1; i <= ItemIdx + len; i++) {
        this.tableData[i].hide = !this.tableData[i].hide;
        // console.log("bianli", this.tableData[i].hide);
      }
    },
    //
    tableRowStyle({ row, rowIndex }) {
      // console.log(row, rowIndex);
      let flag = row.hide ? "none" : "table-row";
      return {
        display: flag
      };
    },
    //  展开
    changePage(val) {
      // console.log(val);
      this.currentPage = val;
      this.searchNew = false;
      //请求
      this.searchInfo();
    },
    changeOpt(val) {
      let that = this;
      let objStr = JSON.stringify(this.realProductList);
      let copyProducts = JSON.parse(objStr);
      that.products = copyProducts;
      that.form.product = "all";
      // 选择渠道
      if (val === "country") {
        if (that.form.country !== "all") {
          let url = that._url2 + "server/personnel_management/get_sign_area/";
          // let url =
          //   "http://106.52.43.196:8003/server/personnel_management/get_sign_area/";
          this.$axios
            .get(url, {
              params: {
                country: that.form.country
              }
            })
            .then(res => {
              that.stores = res.data.data;
            });
        } else {
          that.stores = [];
          that.form.store = "all";
        }
      } else if (val === "site") {
        // console.log("选择站点");
        if (that.form.store !== "all") {
          that.products = [];
          that.productlist.forEach(i => {
            if (i.site === "京汇") {
              // console.log(i);
            }
            if (that.form.store === i.site && that.form.country === i.country) {
              // console.log(i);
              that.products.push(i.product_name);
            }
          });
        }
      }
    },
    // 请求渠道，站点，国家 关联的搜索参数
    // 进来请求渠道及产品编码及类型及仓库搜索参数
    getSearchParams() {
      // http://106.52.43.196:8003/server/order_management/get_search/
      let url = this._url2 + "server/order_management/get_search/";
      this.$axios
        .get(url)
        .then(res => {
          // console.log(res);
          // this.searchlist = res.data.data1;
          this.productlist = res.data.product;
          this.productlist.forEach(i => {
            let value = i.product_code + i.product_name;
            this.realProductList.push(value);
            this.products.push(value);
          });
        })
        .catch(err => {});
      // 渠道国家站点
      let url1 = this._url2 + "server/personnel_management/get_sign_country/";
      // let url1 =
      //   "http://106.52.43.196:8003/server/personnel_management/get_sign_country/";

      this.$axios.get(url1).then(res => {
        this.channels.push(res.data.data1);
        this.countrys = res.data.data;
        // console.log(this.countrys);
      });
    },
    searchInfoBtn() {
      this.searchNew = true;
      this.searchInfo();
    },
    searchInfo() {
      if (this.searchNew) {
        this.currentPage = 1;
      }
      this.loading = true;
      let paramsObj = [];
      // console.log(this.form);
      for (let key in this.form) {
        let item = this.form[key];
        // value :Amazon  key :channel
        // 排除all选项
        if (item.indexOf("all") > -1) {
          continue;
        }
        let obj = {};
        obj["key"] = key;
        obj["value"] = item;
        paramsObj.push(obj);
      }
      let url = this._url2 + "server/order_management/on_way_container/";
      // console.log("122443", paramsObj);
      this.$axios
        .get(url, {
          params: {
            data: JSON.stringify(paramsObj),
            page: this.currentPage
          }
        })
        .then(res => {
          // console.log(res);
          // this.tableData = res.data.data;
          this.totalNum = res.data.total_num;
          const data1 = res.data.data;
          this.warehouses_list = data1.header;
          this.InitData(data1);
          this.loading = false;
        });
    },
    clickRow(row, rowIdx) {
      if (!row.index) {
        return;
      }
      // console.log(row, rowIdx);
      // console.log("点击行");
      // console.log(row);
      let ItemIdx = this.tableData.findIndex(i => {
        return i.index === row.index;
      });
      let Item = this.tableData[ItemIdx];
      let len = Item.sku_data.length;
      // console.log(Item, len);
      // ItemIdx :1  len :3 2-4
      for (let i = ItemIdx + 1; i <= ItemIdx + len; i++) {
        this.tableData[i].hide = !this.tableData[i].hide;
        // console.log("bianli", this.tableData[i].hide);
      }
    },
    getAllData() {
      this.loading = true;
      // http://106.52.43.196:8003/server/order_management/on_way_container/
      let url = this._url2 + "server/order_management/on_way_container/";
      this.$axios.get(url).then(res => {
        const data = res.data.data;
        this.warehouses_list = data.header;
        this.totalNum = res.data.total_num;
        this.InitData(data);
        this.loading = false;
      });
    },
    InitData(data) {
      this.hj = data.data;
      this.tableData = data.data.filter((item, index) => {
        return index > 0;
      });
      // 处理表格子sku数据

      this.tableData.forEach((i, index) => {
        i["index"] = index + 1 + (Number(this.currentPage) - 1) * 50;
      });
      // this.tableData.forEach((item, idx) => {
      //   let skuItem = item.sku_data || [];
      //   if (skuItem.length) {
      //     skuItem.forEach((i, j) => {
      //       let obj = {};
      //       for (let key in i) {
      //         obj[key] = i[key];
      //       }
      //       obj["hide"] = true;
      //       this.tableData.splice(idx + 1, 0, obj);
      //     });
      //     // console.log(this.tableData);
      //   }
      // });
      for (let i = 0; i < this.tableData.length; i++) {
        console.log("表格长度", this.tableData.length);
        let item = this.tableData[i];
        let skuItem = item.sku_data || [];
        if (skuItem.length) {
          skuItem.forEach((sku, skuIdx) => {
            let obj = {};
            for (let key in sku) {
              obj[key] = sku[key];
            }
            obj["hide"] = true;
            console.log("插入的位置", skuIdx + i + 1);
            this.tableData.splice(skuIdx + i + 1, 0, obj);
          });
        }
      }
      // 编辑首行总计
      if (this.currentPage === 1) {
        for (let i in this.tableData[0]) {
          this.zonji[i] = "";
        }
        this.zonji.product_code = "总计";
        this.zonji.fba = this.hj[0].fba_all_num;
        this.zonji.fbm = this.hj[0].fbm_all_num;
        this.zonji.stock_num = this.hj[0].stock_all_num;
      }
      this.tableData.unshift(this.zonji);
    },
    headerStyle({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0 && columnIndex == 0) {
        return "overflow:initial";
      }
    },
    indexMethod(index) {
      if (index == 0) {
        index = "";
      }
      return index;
    }
  },
  created() {
    // 获取下拉数据
    this.getSearchParams();
    // // 获取全部数据
    this.getAllData();
  }
};
</script>
<style scoped>
.InTransitManage {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 40px;
}
.InTransitManage .leftMenu {
  flex: 0 0 260px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 5px;
  padding: 45px 15px;
  height: 500px;
  margin: 0 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.InTransitManage .rightMenu {
  flex: 1;
  /* height: 86vh; */
  overflow: hidden;
  margin: 0 auto;
  margin-right: 10px;
  position: relative;
}
.InTransitManage .el-table tr {
  height: 30px;
}
.InTransitManage table td::after,
.InTransitManage table th::after {
  content: "";
  display: block;
  width: 1px;
  height: 100%;
  position: absolute;
  right: -1px;
  top: 0;
}
.InTransitManage table td::after {
  background-color: #cccccc;
}
.InTransitManage table th::before,
.InTransitManage table td::before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #cccccc;
  position: absolute;
  left: 0;
  bottom: 0;
}
.InTransitManage h2 {
  font-size: 20px;
  text-align: center;
  padding-bottom: 5px;
  background: #fff;
}
.InTransitManage table {
  border: 1px solid;
  border-collapse: collapse;
  table-layout: fixed;
  width: 800px;
  overflow: scroll;
}
td:first-child,
th:first-child {
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  z-index: 1;
  background: #fff;
}
td:first-child {
  box-shadow: -39px 1px #e5e5e5;
}
td:first-child::after,
th:first-child::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  right: -1px;
  top: 0;
  border: 1px solid #e5e5e5;
  border-left: 0;
  border-top: 0;
}
td:nth-child(-n + 6) {
  color: black;
}
td,
th {
  width: 150px;
  text-align: center;
}
.InTransitManage tr th.fixed-column {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: #fff;
  border: 1px solid;
  z-index: 10;
}
thead tr th:nth-child(n + 1)::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  right: -1px;
  top: -1px;
  border: 1px solid #e5e5e5;
  border-left: 0;
}
.InTransitManage .fixed-column {
  position: sticky;
  background-color: #fff;
}
th:first-child {
  background: #fff;
}
.InTransitManage table th {
  box-shadow: 0 -5px #e5e5e6;
}
.InTransitManage td {
  border: 1px solid;
  position: relative;
  max-width: 200px;
}
.InTransitManage .t_head {
  position: relative;
  width: 160px;
  height: 50px;
}
.InTransitManage .t_head_td {
  overflow: hidden;
}
.InTransitManage table {
  table-layout: fixed;
  margin: 0 auto;
}
.InTransitManage .loadingMask {
  font-size: 16px;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 20;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #5cb6ff;
}
.InTransitManage .loadingMask > span {
  margin-left: 10px;
}
.InTransitManage table td > span {
  width: 200px;
  /* padding: 10px 0; */
  display: block;
  text-align: center;
}
.InTransitManage .on_way_table {
  /* max-width: 80vw; */
  width: 100%;
  overflow: scroll;
  max-height: 79vh;
  background: #fff;
  font-size: 14px;
}
.InTransitManage th.fixed-column:nth-child(2),
.InTransitManage td.fixed-column:nth-child(2) {
  left: 40px;
}
.InTransitManage th.fixed-column:nth-child(3),
.InTransitManage td.fixed-column:nth-child(3) {
  left: 190px;
}
.InTransitManage th.fixed-column:nth-child(4),
.InTransitManage td.fixed-column:nth-child(4) {
  left: 340px;
}
.InTransitManage th.fixed-column:nth-child(5),
.InTransitManage td.fixed-column:nth-child(5) {
  left: 400px;
}
.InTransitManage th.fixed-column:nth-child(6),
.InTransitManage td.fixed-column:nth-child(6) {
  left: 460px;
}

/* 伟群改 */
table tr th:nth-child(1) span,
table tr td:nth-child(1) span,
table tr th:nth-child(1),
table tr td:nth-child(1) {
  width: 40px;
}
table tr th:nth-child(n + 4) span,
table tr td:nth-child(n + 4) span,
table tr th:nth-child(n + 4),
table tr td:nth-child(n + 4) {
  width: 60px;
}
table tbody tr:hover,
table tbody tr:hover > td.fixed-column,
.InTransitManage .fixed-column:hover ~ td {
  background: #eaf7e0;
}
table tr td,
table tr th {
  z-index: 0;
  border: 1px #e5e5e5 solid !important;
}
table tr {
  background-color: #fff;
}
</style>
<style>
.InTransitManage .el-pagination {
  background: rgba(255, 255, 255, 0.9);
}
</style>



